.border_contact {
  position: absolute;
  display: table;
  margin: auto;
  border: 1px solid #888;
  width: 20rem;
  height: 25rem;
  background: linear-gradient(45deg,skyblue,lightblue);
}
input {
  display: block;
  margin: auto;
  width: 150px;
  height: 25px;
  padding-left: 10px;
  outline: none;
  border: none;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
label{text-align: center; display: block; font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;}

button {
  display: block;
  margin: auto;
  margin-top: 1rem;
  width: 150px;
  height: 30px;
  border: 1px solid #888;
  background: none;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-radius: 1rem;
  transition: 0.2s;
  cursor: pointer;
  background-color: snow;
}
button:hover {
  background-color: slateblue;
  color: aliceblue;
  border: none;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

a {
  text-decoration: none;
  color: darkmagenta;
  display: block;
  margin-top: 2rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.border_acc {
  border: 1px solid transparent;
  width: 10rem;
  height: 20rem;
  
  display: table;
  margin: auto;
  float: right;
  
}

table , tr  ,td {border: 1px solid #222; border-collapse: collapse;}